html { font-size: 10px; scroll-behavior: smooth; }

ul { list-style: none; /*убираем маркеры списка*/ margin: 0; /*убираем верхнее и нижнее поле, равное 1em*/ padding-left: 0; /*убираем левый отступ, равный 40px*/ }

a { color: inherit; text-decoration: none !important; }

.container { max-width: 1197px; }

.header { position: absolute; top: 0; left: 0; width: 100%; padding-top: 39px; z-index: 2; }

.header .logo { height: 100%; color: #FFFFFF; font-size: 3.4rem; font-family: 'Raleway'; }

.header .logo :hover { color: #fff; }

.header .logo a { display: flex; align-items: center; }

.header .logo h2 { font-size: 3.4rem; font-family: 'Raleway'; letter-spacing: -0.7px; }

.header nav { display: flex; padding-left: 100px; }

.header nav .menu { display: flex; font-size: 1.4rem; font-family: 'Montserrat'; font-weight: 300; color: #fff; }

.header nav .menu .menu-item { letter-spacing: -0.06px; }

.header nav .menu .menu-item a:hover { color: #c7b299 !important; }

.header nav .menu .menu-item:not(:last-child) { padding-right: 28px; }

.header nav .menu .menu-item:not(:last-child):hover { color: #c7b299; }

.header nav .menu .menu-logos { display: none; }

.header .mobile-menu { display: none; }

.header .mobile-menu .menu-btn { display: flex; flex-direction: column; justify-content: center; align-items: center; height: 40px; width: 40px; background: white; border-radius: 50px; position: fixed; top: 45px; }

.header .mobile-menu .menu-btn div { width: 30px; height: 2px; background: #0b0b0b; }

.header .mobile-menu .menu-btn div:not(:last-child) { margin-bottom: 5px; }

.header .logos { display: flex; justify-content: space-between; }

.header .logos li { font-size: 2rem; color: #fff; }

.header .logos li :hover { text-decoration: none; color: #ffffff; }

#mountain .slide { padding-top: 377px; padding-bottom: 380px; text-align: center; position: relative; background-size: cover; background-position: center; background-repeat: no-repeat; color: #fff; font-family: 'Raleway'; }

#mountain .slide:before { content: ""; position: absolute; top: 0; left: 0; right: 0; width: 100%; height: 100%; background: rgba(0, 0, 0, 0.7); }

#mountain .slide p { font-size: 3.6rem; font-weight: 500; position: relative; }

#mountain .slide h2 { font-weight: 800; font-size: 5.2rem; margin-top: 10px; margin-bottom: 21px; position: relative; }

#mountain .slide span { font-family: 'Montserrat'; font-weight: 300; font-size: 1.6rem; line-height: 21px; word-spacing: -0.2px; position: relative; }

#mountain .slide .main-click { position: relative; width: 170px; height: 53px; background: #998675; font-weight: 900; font-size: 1.4rem; display: flex; justify-content: center; align-items: center; margin: 53px auto 0; border-radius: 5px; border-bottom: 3px solid #736357; }

#mountain .slide .main-click:hover { background: bisque; color: #34ce57; border-bottom: 3px solid #bc9c85; }

#mountain .owl-dots { position: absolute; bottom: 34px; left: 0; right: 0; }

#mountain .owl-theme .owl-dots .owl-dot span { margin-left: 4px; }

#mountain .owl-theme .owl-dots .owl-dot.active span { background: none; border: 1px solid #d3c1ad; width: 14px; height: 14px; }

#mountain .owl-nav span { display: none; }

#mountain .owl-nav .owl-prev { position: absolute; top: 46%; left: 7.5%; background-image: url(../img/left.png) !important; background-repeat: no-repeat !important; background-position: center; -webkit-background-size: contain; background-size: contain; height: 75px; width: 45px; margin-right: auto; }

#mountain .owl-nav .owl-prev:hover { background-image: url("../img/arrow-left.png") !important; }

#mountain .owl-nav .owl-next { position: absolute; top: 46%; right: 7.4%; background-image: url(../img/right.png) !important; background-repeat: no-repeat !important; background-position: center; -webkit-background-size: contain; background-size: contain; height: 75px; width: 45px; margin-left: auto; }

#mountain .owl-nav .owl-next:hover { background-image: url("../img/arrow-right.png") !important; }

#about-us { padding: 71px 0 108px; }

#about-us .box-real { font-family: 'Raleway'; text-align: center; height: 200px; }

#about-us .box-real:before { content: ''; width: 70px; height: 3px; background: #f1eee9; position: absolute; left: 0; right: 0; margin: auto; bottom: -30px; }

#about-us .box-real h2 { font-size: 2.2rem; font-weight: 600; padding: 30px 0 18px; }

#about-us .box-real p { font-family: 'Montserrat'; font-size: 1.4rem; font-weight: 300; color: #8c8c8c; line-height: 23px; }

#about-us .box-real .icon-pencil-alt { font-size: 4.4rem; }

#about-us .box-real .icon-layers { font-size: 4.4rem; }

#about-us .box-real .icon-cog { font-size: 4.4rem; }

#about-us .box-real .icon-folder-open { font-size: 4.4rem; }

#about-us .box-real:hover { cursor: pointer; }

#about-us .box-real:hover i { color: #c7b299; transition: all 0.6s; }

#about-us .box-real:hover h2 { color: #c7b299; transition: all 0.6s; }

#about-us .box-real:hover:before { background: #c7b299; z-index: 3; transition: all 0.6s; }

#about-us_text { background: #fbfaf8; text-align: center; }

#about-us_text h2 { font-family: 'Raleway'; font-size: 3rem; font-weight: 600; padding: 83px 0 19px; }

#about-us_text p { font-family: 'Montserrat'; font-size: 1.6rem; font-weight: 300; padding-bottom: 40px; word-spacing: 1px; line-height: 26px; letter-spacing: -0.15px; }

#about-us_text .about-us_img { display: flex; justify-content: center; margin: -2px; }

#about-us_text .about-us_img img { width: 110%; }

#about-us_foot { padding: 43px 0 40px; background: #211b19; /* Old browsers */ background: -moz-linear-gradient(left, #211b19 0%, #4e3427 100%); /* FF3.6-15 */ background: -webkit-linear-gradient(left, #211b19 0%, #4e3427 100%); /* Chrome10-25,Safari5.1-6 */ background: linear-gradient(to right, #211b19 0%, #4e3427 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */ filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#211b19', endColorstr='#4e3427', GradientType=1); /* IE6-9 */ }

#about-us_foot .left-foot h2 { font-family: "Raleway"; font-size: 2.8rem; color: #fff; }

#about-us_foot .left-foot h2 span { color: #c7b299; }

#about-us_foot .left-foot p { font-family: "Montserrat"; font-size: 1.8rem; color: #615855; line-height: 2; }

#about-us_foot .right-foot { position: relative; }

#about-us_foot .right-foot .button-foot { width: 170px; height: 51px; background: #998675; font-family: "Raleway"; font-weight: 800; font-size: 1.4rem; color: #fff; text-transform: uppercase; display: flex; justify-content: center; align-items: center; border-radius: 3px; border-bottom: 3px solid #736357; margin-left: 200px; margin-top: 4px; }

#about-us_foot .right-foot .button-foot:hover { background: lemonchiffon; color: orangered; border-bottom: 3px solid #bc9c85; }

#portfolio { padding-top: 80px; padding-bottom: 80px; }

#portfolio .portfolio-content { display: flex; flex-direction: column; text-align: center; }

#portfolio .portfolio-content h2 { font-family: 'Raleway'; font-weight: 600; font-size: 3.0rem; color: #555555; margin-bottom: 15px; }

#portfolio .portfolio-content p { font-family: 'Montserrat'; font-weight: 300; font-size: 1.6rem; color: #8c8c8c; line-height: 30px; }

#portfolio .block { margin-bottom: 32px; position: relative; }

#portfolio .block:hover .bg-photo { visibility: visible; transition: all 0.6s; cursor: pointer; }

#portfolio .block:hover .block-content { background: #362f2d; color: #FFFFFF; }

#portfolio .block:hover .block-content p { color: #c7b299; }

#portfolio .block:hover .block-content:before { border-color: transparent transparent #362f2d transparent; }

#portfolio .block-img { height: 238px; width: 100%; }

